function dig(obj, selector) {
    var result = obj;
    const splitter = selector.split('.');

    for (let i = 0; i < splitter.length; i++) {
        if (result == undefined)
            return undefined;
        result = result[splitter[i]];
    }

    return result;
}

export function collect(obj, field) {
    if (typeof (field) === 'function')
        return field(obj);
    else if (typeof (field) === 'string')
        return dig(obj, field);
    else
        return undefined;
}

export function exportExcel(columns, rows, title) {
    const mimeType = 'data:application/vnd.openxmlformats-officedocument.spreadsheetml.sheet';
    const html = renderTable(columns, rows).replace(/ /g, '%20');

    const documentPrefix = title != '' ? title.replace(/ /g, '-') : 'Sheet'
    const d = new Date();

    var dummy = document.createElement('a');
    dummy.href = mimeType + ', ' + html;
    dummy.download = documentPrefix
        + '-' + d.getFullYear() + '-' + (d.getMonth() + 1) + '-' + d.getDate()
        + '-' + d.getHours() + '-' + d.getMinutes() + '-' + d.getSeconds()
        + '.xlsx';
    dummy.click();
}

export function print(columns, rows) {
    let win = window.open("");
    win.document.write(renderTable(columns, rows));
    win.print();
    win.close();
}

function renderTable(columns, rows) {
    let table = '<table><thead>';

    table += '<tr>';
    for (let i = 0; i < columns.length; i++) {
        const column = columns[i];
        console.log(column.label);
        table += '<th>';
        table += column.label;
        table += '</th>';
    }
    table += '</tr>';

    table += '</thead><tbody>';

    for (let i = 0; i < rows.length; i++) {
        const row = rows[i];
        table += '<tr>';
        for (let j = 0; j < columns.length; j++) {
            const column = columns[j];
            table += '<td>';
            table += collect(row, column.field);
            table += '</td>';
        }
        table += '</tr>';
    }

    table += '</tbody></table>';
    // console.log(table)
    return table;
}

export const csvGenerator = (totalData, headerToShow, fileName) => {
    let actualHeaderKey = Object.keys(totalData[0]);
    let data = totalData || null;
    if (data == null || !data.length) {
        return null;
    }
    let columnDelimiter = ",";
    let lineDelimiter = "\n";
    let keys = headerToShow.label;
    let result = "";
    result += keys.join(columnDelimiter);
    result += lineDelimiter;
    data.forEach(function (item) {
        let ctr = 0;
        actualHeaderKey.forEach(function (key) {
            if (ctr > 0) result += columnDelimiter;
            if (Array.isArray(item[key])) {
                let arrayItem =
                    item[key] && item[key].length > 0
                        ? '"' + item[key].join(",") + '"'
                        : "-";
                result += arrayItem;
            } else if (typeof item[key] == "string") {
                let strItem = item[key] ? '"' + item[key] + '"' : "-";
                result += strItem ? strItem.replace(/\s{2,}/g, " ") : strItem;
            } else {
                let strItem = item[key] + "";
                result += strItem ? strItem.replace(/,/g, "") : strItem;
            }

            ctr++;
        });
        result += lineDelimiter;
    });

    if (result == null) return;

    var blob = new Blob([result]);
    if (navigator.msSaveBlob) {
        // IE 10+
        navigator.msSaveBlob(blob, exportedFilenmae);
    } else if (navigator.userAgent.match(/iPhone|iPad|iPod/i)) {
        var hiddenElement = window.document.createElement("a");
        hiddenElement.href = "data:text/csv;charset=utf-8," + encodeURI(result);
        hiddenElement.target = "_blank";
        hiddenElement.download = fileName + '.csv';
        hiddenElement.click();
    } else {
        let link = document.createElement("a");
        if (link.download !== undefined) {
            // Browsers that support HTML5 download attribute
            var url = URL.createObjectURL(blob);
            link.setAttribute("href", url);
            link.setAttribute("download", fileName);
            link.style.visibility = "hidden";
            document.body.appendChild(link);
            link.click();
            document.body.removeChild(link);
        }
    }
};