<script>
  import { DataTable, Dialog, Button } from "smelte";
  import Icon from "svelte-icon";
  import csv from "../svg/csv.svg";
  import excel from "../svg/excel.svg";
  import { exportExcel, print, csvGenerator } from "../export.js";
  let showDialog = false;
  export let title;
  export let columns;
  export let rows;
  export let fields;

  import { get } from "svelte/store";
  import { valuesForm, Field } from "svelte-formly";

  

  let message = "";
  let values = {};
  let color = "#ff3e00";

  function onSubmit() {
    const data = get(valuesForm);
    if (data.isValidForm) {
      values = data.values;
      color = values.color ? values.color : color;
      message = "Congratulation! now your form is valid";
    } else {
      message = "Your form is not valid!";
    }
  }
</script>

<h3>{title}</h3>
<DataTable data={rows} {columns} />
<Dialog persistent="true" bind:value={showDialog}>
  <form
    on:submit|preventDefault={onSubmit}
    class="custom-form"
    style="--theme-color: {color}">
    <Field {fields} />
    <button class="btn btn-primary" type="submit">Submit</button>
  </form>
  <div slot="actions">
    <Button text on:click={() => (showDialog = false)}>Disagree</Button>
    <Button text on:click={() => (showDialog = false)}>Agree</Button>
  </div>
</Dialog>

<div class="py-2">
  <Button on:click={() => (showDialog = true)}>Show dialog</Button>
</div>

<div class="actions md:flex">
  <a
    href="#!"
    class="waves-effect btn-flat nopadding"
    on:click={() => print(columns, rows)}>
    <i class="material-icons text-error-500">print</i>

  </a>
  <a
    href="#!"
    class="waves-effect btn-flat nopadding"
    v-if="this.exportable"
    on:click={() => exportExcel(columns, rows, title)}>
    <Icon data={excel} color="#67efef" size="24px" />
  </a>

  <button on:click={() => csvGenerator(rows, columns, title)}>
    <Icon data={csv} color="#897878" size="24px" />
  </button>
</div>
<form class="w-full max-w-sm">
  <div class="md:flex md:items-center mb-6">
    <div class="md:w-1/3">
      <label class="block text-gray-500 font-bold md:text-right mb-1 md:mb-0 pr-4" for="inline-full-name">
        Full Name
      </label>
    </div>
    <div class="md:w-2/3">
      <input class="bg-gray-200 appearance-none border-2 border-gray-200 rounded w-full py-2 px-4 text-gray-700 leading-tight focus:outline-none focus:bg-white focus:border-purple-500" id="inline-full-name" type="text" value="Jane Doe">
    </div>
  </div>
  <div class="md:flex md:items-center mb-6">
    <div class="md:w-1/3">
      <label class="block text-gray-500 font-bold md:text-right mb-1 md:mb-0 pr-4" for="inline-username">
        Password
      </label>
    </div>
    <div class="md:w-2/3">
      <input class="bg-gray-200 appearance-none border-2 border-gray-200 rounded w-full py-2 px-4 text-gray-700 leading-tight focus:outline-none focus:bg-white focus:border-purple-500" id="inline-username" type="password" placeholder="******************">
    </div>
  </div>
  <div class="md:flex md:items-center mb-6">
    <div class="md:w-1/3"></div>
    <label class="md:w-2/3 block text-gray-500 font-bold">
      <input class="mr-2 leading-tight" type="checkbox">
      <span class="text-sm">
        Send me your newsletter!
      </span>
    </label>
  </div>
  <div class="md:flex md:items-center">
    <div class="md:w-1/3"></div>
    <div class="md:w-2/3">
      <button class="shadow bg-purple-500 hover:bg-purple-400 focus:shadow-outline focus:outline-none text-white font-bold py-2 px-4 rounded" type="button">
        Sign Up
      </button>
    </div>
  </div>
</form>